import React from 'react';
import { Card, Breadcrumb } from 'antd';
import { HomeOutlined, TrophyOutlined } from '@ant-design/icons';
import { Link } from 'react-router-dom';
import RankingDataLoader from '../components/Ranking/RankingDataLoader';

// process.env.PUBLIC_URLGitHub Pages
const RankingPage = () => {
  // 
  const teamRankings = [
    { url: `${process.env.PUBLIC_URL}/ranking.csv`, type: 'csv' }
  ];
  
  const rankingStyle = {
    display: 'flex',
    flexWrap: 'wrap',
    justifyContent: 'flex-start'
  };
  
  return (
    <div>
      <Breadcrumb style={{ marginBottom: '16px' }}>
        <Breadcrumb.Item>
          <Link to="/">
            <HomeOutlined /> 
          </Link>
        </Breadcrumb.Item>
        <Breadcrumb.Item>
          <TrophyOutlined /> 
        </Breadcrumb.Item>
      </Breadcrumb>
      
      <Card
        bordered={false}
        style={{ borderRadius: '8px', boxShadow: '0 2px 8px rgba(0,0,0,0.08)' }}
      >
        <h2 style={{ marginBottom: '20px' }}></h2>
        <div style={rankingStyle}>
          {teamRankings.map((ranking, index) => (
            <RankingDataLoader 
              key={index}
              dataSource={ranking.url} 
              type={ranking.type}
            />
          ))}
        </div>
      </Card>
    </div>
  );
};

export default RankingPage; 