import React, { useState, useEffect } from 'react';
import { Row, Col, Card, Typography, List, Avatar, Spin } from 'antd';
import { 
  StarOutlined, 
  TrophyOutlined, 
  FireOutlined, 
  RightOutlined 
} from '@ant-design/icons';
import TeamProgressCard from '../components/TeamProgressCard';
import AchievementNotification from '../components/AchievementNotification';
import CelebrationEffect from '../components/CelebrationEffect';
import TeamMarquee from '../components/TeamMarquee';
import ContributionDisplay from '../components/ContributionDisplay';
import { 
  fetchTeamProgress, 
  fetchPersonalStats, 
  fetchTeamRanking, 
  fetchMonthlyHonors,
  teamAchievementsData 
} from '../services/api';

const { Text, Title } = Typography;

// 
const userAchievements = [
  {
    id: 1,
    description: 'CPI$0.5',
    points: 2
  },
  {
    id: 2,
    description: '1m rev/install 20%',
    points: 3
  },
  {
    id: 3,
    description: 'R QS0.5',
    points: 1
  }
];

const Dashboard = () => {
  const [teamProgress, setTeamProgress] = useState([]);
  const [personalStats, setPersonalStats] = useState({
    growthPoints: '',
    completedTasks: 0,
    ranking: 0,
    daysStreak: 0
  });
  const [teamRanking, setTeamRanking] = useState([]);
  const [monthlyHonors, setMonthlyHonors] = useState([]);
  const [loading, setLoading] = useState(true);
  
  // 
  const [showNotification, setShowNotification] = useState(false);
  const [currentAchievementIndex, setCurrentAchievementIndex] = useState(0);
  const [showCelebration, setShowCelebration] = useState(false);
  const [celebrationLevel, setCelebrationLevel] = useState(0);

  useEffect(() => {
    const fetchData = async () => {
      try {
        const [
          progressData,
          statsData,
          rankingData,
          honorsData
        ] = await Promise.all([
          fetchTeamProgress(),
          fetchPersonalStats(),
          fetchTeamRanking(),
          fetchMonthlyHonors()
        ]);

        setTeamProgress(progressData);
        setPersonalStats(statsData);
        setTeamRanking(rankingData);
        setMonthlyHonors(honorsData);
        setLoading(false);
        
        // 
        setTimeout(() => {
          if (userAchievements.length > 0) {
            setShowNotification(true);
          }
        }, 1000);
      } catch (error) {
        console.error(':', error);
        setLoading(false);
      }
    };

    fetchData();
  }, []);
  
  // ""
  const handleNextAchievement = () => {
    // 
    const currentAchievement = userAchievements[currentAchievementIndex];
    setCelebrationLevel(currentAchievement.points);
    setShowCelebration(true);
    
    // 
    setShowNotification(false);
    
    // 
    setTimeout(() => {
      setShowCelebration(false);
      
      if (currentAchievementIndex < userAchievements.length - 1) {
        setCurrentAchievementIndex(currentAchievementIndex + 1);
        setShowNotification(true);
      }
    }, 3000); // 3
  };

  // 
  const getAvatarColor = (index) => {
    const colors = ['#722ED1', '#13C2C2', '#1677FF', '#FA8C16', '#EB2F96'];
    return colors[index % colors.length];
  };

  if (loading) {
    return (
      <div style={{ display: 'flex', justifyContent: 'center', alignItems: 'center', height: 'calc(100vh - 112px)' }}>
        <Spin size="large" tip="..." />
      </div>
    );
  }

  return (
    <div className="dashboard-container" style={{ padding: '16px 24px', backgroundColor: '#F0F2F5' }}>
      {/*  */}
      {showNotification && (
        <AchievementNotification
          visible={showNotification}
          achievement={userAchievements[currentAchievementIndex]}
          onNext={handleNextAchievement}
        />
      )}
      
      {/*  */}
      {showCelebration && (
        <CelebrationEffect level={celebrationLevel} />
      )}
      
      {/*  */}
      <ContributionDisplay />
      
      {/*  */}
      <TeamMarquee achievements={teamAchievementsData} />
      
      {/*  */}
      <div style={{ marginBottom: '24px' }}>
        <Title level={4} style={{ marginBottom: '16px' }}></Title>
        <Row gutter={16}>
          <Col span={6}>
            <Card
              bordered={false}
              className="stat-card"
              style={{ 
                height: '160px', 
                padding: '0', 
                overflow: 'hidden',
                display: 'flex',
                flexDirection: 'column'
              }}
            >
              <div style={{ 
                fontSize: '15px', 
                color: '#666', 
                padding: '12px 16px',
                borderBottom: '1px solid #f0f0f0',
                backgroundColor: '#fafafa'
              }}>
                DSP
              </div>
              <div style={{ 
                flex: 1, 
                display: 'flex', 
                flexDirection: 'column', 
                justifyContent: 'center',
                alignItems: 'center',
                padding: '12px 16px'
              }}>
                <Text style={{ fontSize: '28px', fontWeight: 'bold', color: '#52C41A', marginBottom: '8px' }}>
                  {personalStats.growthPoints}
                  <span style={{ marginLeft: '4px', fontSize: '18px' }}></span>
                </Text>
                <div style={{ fontSize: '13px', color: '#999', textAlign: 'center', lineHeight: '1.4' }}>
                  10%
                </div>
              </div>
            </Card>
          </Col>
          
          <Col span={6}>
            <Card
              bordered={false}
              className="stat-card"
              style={{ 
                height: '160px', 
                padding: '0', 
                overflow: 'hidden',
                display: 'flex',
                flexDirection: 'column'
              }}
            >
              <div style={{ 
                fontSize: '15px', 
                color: '#666', 
                padding: '12px 16px',
                borderBottom: '1px solid #f0f0f0',
                backgroundColor: '#fafafa'
              }}>
                
              </div>
              <div style={{ 
                flex: 1, 
                display: 'flex', 
                flexDirection: 'column', 
                justifyContent: 'center',
                alignItems: 'center',
                padding: '12px 16px'
              }}>
                <Text style={{ fontSize: '28px', fontWeight: 'bold', color: '#1677FF', marginBottom: '8px' }}>
                  {personalStats.completedTasks}
                  <StarOutlined style={{ fontSize: '18px', marginLeft: '4px' }} />
                </Text>
                <div style={{ fontSize: '13px', color: '#999', textAlign: 'center', lineHeight: '1.4' }}>
                  91%
                </div>
              </div>
            </Card>
          </Col>
          
          <Col span={6}>
            <Card
              bordered={false}
              className="stat-card"
              style={{ 
                height: '160px', 
                padding: '0', 
                overflow: 'hidden',
                display: 'flex',
                flexDirection: 'column'
              }}
            >
              <div style={{ 
                fontSize: '15px', 
                color: '#666', 
                padding: '12px 16px',
                borderBottom: '1px solid #f0f0f0',
                backgroundColor: '#fafafa'
              }}>
                
              </div>
              <div style={{ 
                flex: 1, 
                display: 'flex', 
                flexDirection: 'column', 
                justifyContent: 'center',
                alignItems: 'center',
                padding: '12px 16px'
              }}>
                <Text style={{ fontSize: '28px', fontWeight: 'bold', color: '#722ED1', marginBottom: '8px' }}>
                  {personalStats.ranking}
                  <TrophyOutlined style={{ fontSize: '18px', marginLeft: '4px' }} />
                </Text>
                <div style={{ fontSize: '13px', color: '#999', textAlign: 'center', lineHeight: '1.4' }}>
                  68%
                </div>
              </div>
            </Card>
          </Col>
          
          <Col span={6}>
            <Card
              bordered={false}
              className="stat-card"
              style={{ 
                height: '160px', 
                padding: '0', 
                overflow: 'hidden',
                display: 'flex',
                flexDirection: 'column'
              }}
            >
              <div style={{ 
                fontSize: '15px', 
                color: '#666', 
                padding: '12px 16px',
                borderBottom: '1px solid #f0f0f0',
                backgroundColor: '#fafafa'
              }}>
                
              </div>
              <div style={{ 
                flex: 1, 
                display: 'flex', 
                flexDirection: 'column', 
                justifyContent: 'center',
                alignItems: 'center',
                padding: '12px 16px'
              }}>
                <Text style={{ fontSize: '28px', fontWeight: 'bold', color: '#FA8C16', marginBottom: '8px' }}>
                  {personalStats.daysStreak}
                  <span style={{ marginLeft: '4px', fontSize: '18px' }}></span>
                  <FireOutlined style={{ fontSize: '18px', marginLeft: '4px' }} />
                </Text>
                <div style={{ fontSize: '13px', color: '#999', textAlign: 'center', lineHeight: '1.4' }}>
                  
                </div>
              </div>
            </Card>
          </Col>
        </Row>
      </div>
      
      {/*  */}
      <Row gutter={16} style={{ marginBottom: '24px' }}>
        <Col span={16}>
          <Card
            title=""
            style={{ height: '100%' }}
            extra={<a href="#more" style={{ color: '#1677FF', fontSize: '13px' }}></a>}
          >
            {teamProgress.map(team => (
              <TeamProgressCard key={team.id} team={team} />
            ))}
          </Card>
        </Col>
        
        <Col span={8}>
          <Card
            title=""
            style={{ height: '100%' }}
            extra={<a href="#more" style={{ color: '#1677FF', fontSize: '13px' }}></a>}
          >
            <List
              dataSource={teamRanking}
              renderItem={(item, index) => (
                <List.Item 
                  key={item.id}
                  style={{ 
                    padding: '12px 0', 
                    borderBottom: index === teamRanking.length - 1 ? 'none' : '1px solid #f0f0f0'
                  }}
                >
                  <div style={{ display: 'flex', justifyContent: 'space-between', width: '100%', alignItems: 'center' }}>
                    <div style={{ display: 'flex', flexDirection: 'column' }}>
                      <div style={{ fontSize: '14px', fontWeight: 'bold', marginBottom: '4px' }}>
                        <span style={{ 
                          display: 'inline-block', 
                          width: '20px', 
                          height: '20px', 
                          lineHeight: '20px', 
                          textAlign: 'center', 
                          backgroundColor: index < 3 ? '#3370FF' : '#E5E5E5', 
                          color: index < 3 ? 'white' : '#666',
                          borderRadius: '4px',
                          marginRight: '8px',
                          fontSize: '12px'
                        }}>
                          {index + 1}
                        </span>
                        {item.name}
                      </div>
                      <div style={{ fontSize: '12px', color: '#999' }}>
                        {item.time}
                      </div>
                    </div>
                    <div style={{ color: '#52C41A', fontWeight: 'bold' }}>
                      {item.increase}
                    </div>
                  </div>
                </List.Item>
              )}
            />
          </Card>
        </Col>
      </Row>
      
      {/*  */}
      <Card
        title=""
        extra={<a href="#more" style={{ color: '#1677FF', fontSize: '13px' }}> <RightOutlined style={{ fontSize: '10px' }} /></a>}
      >
        <List
          grid={{ gutter: 16, column: 5 }}
          dataSource={monthlyHonors}
          renderItem={(item, index) => (
            <List.Item key={item.id}>
              <div style={{ textAlign: 'center' }}>
                <Avatar
                  size={64}
                  style={{ backgroundColor: getAvatarColor(index), marginBottom: '12px' }}
                >
                  {item.name.slice(0, 1)}
                </Avatar>
                <div style={{ fontWeight: 'bold', fontSize: '15px', marginBottom: '4px' }}>
                  {item.name}
                </div>
                <div style={{ color: '#666', fontSize: '13px', marginBottom: '4px' }}>
                  {item.dept}
                </div>
                <div style={{ color: '#ff6b45', fontSize: '14px', fontWeight: 'bold' }}>
                  {item.rank}
                </div>
              </div>
            </List.Item>
          )}
        />
      </Card>
    </div>
  );
};

export default Dashboard; 