import React from 'react';
import { Modal, Button } from 'antd';
import { TrophyOutlined } from '@ant-design/icons';

const AchievementNotification = ({ 
  visible, 
  achievement, 
  onClose, 
  onNext 
}) => {
  // 
  const getTrophyColor = (points) => {
    switch(points) {
      case 1: 
        return '#CD7F32'; // 
      case 2: 
        return '#C0C0C0'; // 
      case 3: 
        return '#FFD700'; // 
      default: 
        return '#CD7F32';
    }
  };

  return (
    <Modal
      open={visible}
      footer={null}
      closable={false}
      centered
      width={400}
      bodyStyle={{
        backgroundColor: '#FFEBEE', // 
        borderRadius: '8px',
        padding: '24px',
        textAlign: 'center'
      }}
      style={{ borderRadius: '8px' }}
      maskStyle={{ backgroundColor: 'rgba(0, 0, 0, 0.65)' }}
    >
      <div style={{ display: 'flex', flexDirection: 'column', alignItems: 'center' }}>
        {/*  */}
        <TrophyOutlined style={{ 
          fontSize: '64px', 
          color: getTrophyColor(achievement.points),
          marginBottom: '16px'
        }} />
        
        {/*  */}
        <h2 style={{ marginBottom: '16px', fontSize: '20px' }}>
          
        </h2>
        
        {/*  */}
        <p style={{ 
          fontSize: '16px', 
          marginBottom: '24px',
          padding: '0 16px'
        }}>
          {achievement.description}
        </p>
        
        {/*  */}
        <div style={{ 
          marginBottom: '24px',
          backgroundColor: 'rgba(255, 255, 255, 0.6)',
          padding: '8px 16px',
          borderRadius: '16px'
        }}>
          <span style={{ fontWeight: 'bold' }}>: </span>
          <span style={{ 
            color: '#f5222d', 
            fontSize: '18px', 
            fontWeight: 'bold'
          }}>
            +{achievement.points}
          </span>
        </div>
        
        {/*  */}
        <Button 
          type="primary" 
          size="large"
          onClick={onNext}
          style={{ 
            backgroundColor: '#f5222d', 
            borderColor: '#f5222d',
            borderRadius: '20px',
            paddingLeft: '32px',
            paddingRight: '32px',
            height: '40px'
          }}
        >
          
        </Button>
      </div>
    </Modal>
  );
};

export default AchievementNotification; 