import React, { useState } from 'react';
import { Card, Row, Col, Statistic, Button, Modal, List, Avatar, Tabs } from 'antd';
import { DollarCircleFilled, TeamOutlined, UserOutlined, TrophyOutlined } from '@ant-design/icons';
import { useNavigate } from 'react-router-dom';

// 
const weeklyRankings = [
  { id: 1, name: '', points: 125, avatar: null, color: '#1677FF' },
  { id: 2, name: '', points: 118, avatar: null, color: '#722ED1' },
  { id: 3, name: '', points: 110, avatar: null, color: '#13C2C2' },
  { id: 4, name: '', points: 105, avatar: null, color: '#FA8C16' },
  { id: 5, name: '', points: 98, avatar: null, color: '#EB2F96' },
];

const monthlyRankings = [
  { id: 1, name: '', points: 458, avatar: null, color: '#1677FF' },
  { id: 2, name: '', points: 432, avatar: null, color: '#722ED1' },
  { id: 3, name: '', points: 411, avatar: null, color: '#13C2C2' },
  { id: 4, name: '', points: 395, avatar: null, color: '#FA8C16' },
  { id: 5, name: '', points: 379, avatar: null, color: '#EB2F96' },
];

const teamWeeklyRankings = [
  { id: 1, name: '', points: 560, avatar: null, color: '#1677FF' },
  { id: 2, name: '', points: 542, avatar: null, color: '#722ED1' },
  { id: 3, name: '', points: 518, avatar: null, color: '#13C2C2' },
  { id: 4, name: '', points: 495, avatar: null, color: '#FA8C16' },
  { id: 5, name: '', points: 487, avatar: null, color: '#EB2F96' },
];

const teamMonthlyRankings = [
  { id: 1, name: '', points: 2340, avatar: null, color: '#1677FF' },
  { id: 2, name: '', points: 2285, avatar: null, color: '#722ED1' },
  { id: 3, name: '', points: 2150, avatar: null, color: '#13C2C2' },
  { id: 4, name: '', points: 2095, avatar: null, color: '#FA8C16' },
  { id: 5, name: '', points: 2010, avatar: null, color: '#EB2F96' },
];

const ContributionDisplay = () => {
  const [isModalOpen, setIsModalOpen] = useState(false);
  const [modalType, setModalType] = useState('personal');
  const [timeRange, setTimeRange] = useState('week');
  const navigate = useNavigate();
  
  const showModal = (type, range) => {
    setModalType(type);
    setTimeRange(range);
    setIsModalOpen(true);
  };

  const handleCancel = () => {
    setIsModalOpen(false);
  };
  
  // 
  const goToRanking = (type) => {
    navigate('/ranking', { state: { defaultTab: type } });
  };

  // 
  const getRankingData = () => {
    if (modalType === 'personal') {
      return timeRange === 'week' ? weeklyRankings : monthlyRankings;
    } else {
      return timeRange === 'week' ? teamWeeklyRankings : teamMonthlyRankings;
    }
  };
  
  // /
  const getRank = (type, range) => {
    if (type === 'personal') {
      return range === 'week' ? 3 : 2;
    } else {
      return range === 'week' ? 1 : 1;
    }
  };

  const buttonStyle = {
    marginBottom: '8px',
    backgroundColor: 'rgba(255,255,255,0.2)',
    borderColor: 'transparent',
    color: '#000',
    fontWeight: 'bold',
    textShadow: '0 1px 2px rgba(255,255,255,0.3)'
  };

  return (
    <Row gutter={16} style={{ marginBottom: '24px' }}>
      {/*  */}
      <Col span={12}>
        <Card 
          bordered={false}
          style={{ 
            height: '120px', 
            borderRadius: '8px', 
            boxShadow: '0 2px 8px rgba(0,0,0,0.08)',
            background: 'linear-gradient(135deg, #0F2656 0%, #1890ff 100%)',
            padding: 0
          }}
          bodyStyle={{ 
            height: '100%', 
            padding: '12px 16px', 
            display: 'flex', 
            alignItems: 'center' 
          }}
        >
          <div style={{ 
            display: 'flex', 
            justifyContent: 'space-between', 
            width: '100%', 
            alignItems: 'center'
          }}>
            <div style={{ display: 'flex', flexDirection: 'column', justifyContent: 'center' }}>
              <div style={{ fontSize: '16px', color: 'white', fontWeight: 'bold', display: 'flex', alignItems: 'center', marginBottom: '10px' }}>
                <TeamOutlined style={{ marginRight: '8px' }} />
                <span> </span>
              </div>
              
              <div style={{ display: 'flex', alignItems: 'center', marginBottom: '10px' }}>
                <DollarCircleFilled style={{ fontSize: '32px', color: '#FFD700', marginRight: '12px' }} />
                <span style={{ fontSize: '28px', color: 'white', fontWeight: 'bold' }}>2,340</span>
              </div>
              
              <div style={{ color: 'rgba(255,255,255,0.85)', fontSize: '14px' }}>
                : <TrophyOutlined style={{ color: '#FFD700' }} /> 1
              </div>
            </div>
            
            <div style={{ display: 'flex', flexDirection: 'column' }}>
              <Button 
                type="default" 
                size="small" 
                style={buttonStyle}
                onClick={() => goToRanking('team')}
              >
                
              </Button>
              <Button 
                type="default" 
                size="small"
                style={{ ...buttonStyle, marginBottom: 0 }}
                onClick={() => showModal('team', 'month')}
              >
                
              </Button>
            </div>
          </div>
        </Card>
      </Col>
      
      {/*  */}
      <Col span={12}>
        <Card 
          bordered={false}
          style={{ 
            height: '120px', 
            borderRadius: '8px', 
            boxShadow: '0 2px 8px rgba(0,0,0,0.08)',
            background: 'linear-gradient(135deg, #722ED1 0%, #a37bfc 100%)',
            padding: 0
          }}
          bodyStyle={{ 
            height: '100%', 
            padding: '12px 16px', 
            display: 'flex', 
            alignItems: 'center' 
          }}
        >
          <div style={{ 
            display: 'flex', 
            justifyContent: 'space-between', 
            width: '100%', 
            alignItems: 'center'
          }}>
            <div style={{ display: 'flex', flexDirection: 'column', justifyContent: 'center' }}>
              <div style={{ fontSize: '16px', color: 'white', fontWeight: 'bold', display: 'flex', alignItems: 'center', marginBottom: '10px' }}>
                <UserOutlined style={{ marginRight: '8px' }} />
                <span> </span>
              </div>
              
              <div style={{ display: 'flex', alignItems: 'center', marginBottom: '10px' }}>
                <DollarCircleFilled style={{ fontSize: '32px', color: '#FFD700', marginRight: '12px' }} />
                <span style={{ fontSize: '28px', color: 'white', fontWeight: 'bold' }}>458</span>
              </div>
              
              <div style={{ color: 'rgba(255,255,255,0.85)', fontSize: '14px' }}>
                : <TrophyOutlined style={{ color: '#C0C0C0' }} /> 2
              </div>
            </div>
            
            <div style={{ display: 'flex', flexDirection: 'column' }}>
              <Button 
                type="default" 
                size="small" 
                style={buttonStyle}
                onClick={() => showModal('personal', 'week')}
              >
                
              </Button>
              <Button 
                type="default" 
                size="small"
                style={{ ...buttonStyle, marginBottom: 0 }}
                onClick={() => showModal('personal', 'month')}
              >
                
              </Button>
            </div>
          </div>
        </Card>
      </Col>
      
      {/*  */}
      <Modal
        title={
          <div>
            {modalType === 'personal' ? '' : ''}
            <span style={{ fontSize: '14px', color: '#999', marginLeft: '8px' }}>
              ({timeRange === 'week' ? '' : ''})
            </span>
          </div>
        }
        open={isModalOpen}
        onCancel={handleCancel}
        footer={null}
        width={400}
      >
        <Tabs
          defaultActiveKey={timeRange}
          onChange={setTimeRange}
          items={[
            {
              key: 'week',
              label: '',
            },
            {
              key: 'month',
              label: '',
            },
          ]}
        />
        
        <List
          dataSource={getRankingData()}
          renderItem={(item, index) => (
            <List.Item 
              style={{ 
                padding: '12px 0', 
                borderBottom: index === getRankingData().length - 1 ? 'none' : '1px solid #f0f0f0'
              }}
            >
              <div style={{ 
                display: 'flex', 
                justifyContent: 'space-between', 
                width: '100%', 
                alignItems: 'center'
              }}>
                <div style={{ display: 'flex', alignItems: 'center' }}>
                  <div style={{ 
                    width: '28px', 
                    height: '28px', 
                    lineHeight: '28px', 
                    textAlign: 'center', 
                    backgroundColor: index < 3 ? '#3370FF' : '#f5f5f5',
                    color: index < 3 ? 'white' : '#666',
                    borderRadius: '50%',
                    marginRight: '12px',
                    fontWeight: 'bold'
                  }}>
                    {index + 1}
                  </div>
                  
                  <div style={{ display: 'flex', alignItems: 'center' }}>
                    <Avatar
                      style={{ 
                        backgroundColor: item.color,
                        marginRight: '8px'
                      }}
                    >
                      {item.name.charAt(0)}
                    </Avatar>
                    <span style={{ fontWeight: index < 3 ? 'bold' : 'normal' }}>
                      {item.name}
                    </span>
                  </div>
                </div>
                
                <div style={{ 
                  display: 'flex', 
                  alignItems: 'center', 
                  color: index === 0 ? '#f5222d' : (index < 3 ? '#fa8c16' : '#666')
                }}>
                  <DollarCircleFilled 
                    style={{ 
                      color: index === 0 ? '#FFD700' : (index < 3 ? '#fa8c16' : '#666'),
                      marginRight: '4px'
                    }} 
                  />
                  <span style={{ fontWeight: 'bold' }}>{item.points}</span>
                </div>
              </div>
            </List.Item>
          )}
        />
      </Modal>
    </Row>
  );
};

export default ContributionDisplay; 