import React, { useEffect } from 'react';
import confetti from 'canvas-confetti';

const CelebrationEffect = ({ level }) => {
  useEffect(() => {
    // 
    switch(level) {
      case 1: // 
        confetti({
          particleCount: 100,
          spread: 70,
          origin: { y: 0.6 }
        });
        break;
      case 2: // 
        confetti({
          particleCount: 200,
          spread: 100,
          origin: { y: 0.6 }
        });
        
        // 
        setTimeout(() => {
          confetti({
            particleCount: 150,
            spread: 80,
            origin: { y: 0.7 }
          });
        }, 1000);
        break;
      case 3: // 
        // 
        const duration = 5 * 1000;
        const end = Date.now() + duration;
        
        const frame = () => {
          confetti({
            particleCount: 50,
            angle: 60,
            spread: 55,
            origin: { x: 0, y: 0.65 }
          });
          
          confetti({
            particleCount: 50,
            angle: 120,
            spread: 55,
            origin: { x: 1, y: 0.65 }
          });
          
          if (Date.now() < end) {
            requestAnimationFrame(frame);
          }
        };
        
        frame();
        break;
      default:
        confetti({
          particleCount: 100,
          spread: 70,
          origin: { y: 0.6 }
        });
    }
  }, [level]);
  
  // canvascanvas-confetticanvas
  return null;
};

export default CelebrationEffect; 