import React, { useState, useEffect } from 'react';
import Ranking from './Ranking.js';

//  JSON 
const readJSON = async (file) => {
  try {
    console.log("JSON:", file);
    const response = await fetch(file);
    if (!response.ok) {
      throw new Error(`HTTP! : ${response.status}`);
    }
    return await response.json();
  } catch (error) {
    console.error("JSON:", error, ":", file);
    throw error;
  }
};

// CSV - 
const convertCSVToRankingData = (csvData) => {
  // CSV
  const lines = csvData.trim().split('\n');
  const headers = lines[0].split(',');
  
  console.log("CSV:", headers);
  console.log("CSV:", lines[1]);
  
  // 
  const firstDataRow = lines[1]?.split(',') || [];
  const title = firstDataRow[0] || '';
  const month = firstDataRow[1] || '3';
  const rankTotal = parseInt(firstDataRow[2] || '5', 10);
  const rankSubTitle = firstDataRow[3] || `${month}`;
  
  // 5 (4-8CSV5-9)
  const col1Header = headers[4] || '';  // CSV51
  const col2Header = headers[5] || '';  // CSV62
  const col3Header = headers[6] || '';     // CSV73
  const col4Header = headers[7] || '';     // CSV84
  const col5Header = headers[8] || '';     // CSV95
  
  console.log("5:", col1Header, col2Header, col3Header, col4Header, col5Header);
  
  // B-E
  const dataTypes = [
    { id: 1, type_name: col2Header }, // 2
    { id: 2, type_name: col3Header }, // 3
    { id: 3, type_name: col4Header }, // 4
    { id: 4, type_name: col5Header }  // 5
  ];
  
  // 
  const details = {};
  lines.slice(1).forEach((line, index) => {
    const values = line.split(',');
    
    // 9
    if (values.length < 9) {
      console.log(":", values);
      return;
    }
    
    const rank = index + 1;
    
    // 5
    const col1Value = values[4] || '';  // CSV51
    const col2Value = values[5] || '';  // CSV62
    const col3Value = values[6] || '';  // CSV73
    const col4Value = values[7] || '';  // CSV84
    const col5Value = values[8] || '';  // CSV95
    
    console.log(`${rank}:`, col1Value, col2Value, col3Value, col4Value, col5Value);
    
    // B-E
    const dataValues = [
      { id: 1, data_value: col2Value }, // 2
      { id: 2, data_value: col3Value }, // 3
      { id: 3, data_value: col4Value }, // 4
      { id: 4, data_value: col5Value }  // 5
    ];
    
    // 
    details[`rank${rank}`] = [
      {
        id: 1000 + rank,
        user_name: col1Value, // 1()
        data_values: dataValues
      }
    ];
  });
  
  // details
  if (Object.keys(details).length === 0) {
    console.error(": !");
    console.log("CSV:", lines.length);
    console.log("CSV:", lines[0]);
    console.log("CSV:", lines[1]);
  }
  
  // 
  const result = {
    rank_title: title,
    rank_sub_title: rankSubTitle,
    rank_total: rankTotal,
    project: {
      project: "",
      month: {
        month: month
      }
    },
    data_types: dataTypes,  // 2-5
    team_name: col1Header,  // 1()
    details: details
  };
  
  console.log(":", result);
  return result;
};

//  CSV 
const readCSV = async (file) => {
  try {
    console.log("CSV:", file);
    const response = await fetch(file);
    if (!response.ok) {
      console.error(`CSVHTTP! : ${response.status}`);
      throw new Error(`CSV: ${file}, : ${response.status}`);
    }
    return await response.text();
  } catch (error) {
    console.error("CSV:", error, ":", file);
    // GitHub Pages
    if (window.location.hostname.includes('github.io')) {
      try {
        console.log("CSV...");
        // 
        const alternativePath = file.replace(process.env.PUBLIC_URL, '');
        console.log(":", alternativePath);
        const response = await fetch(alternativePath);
        if (!response.ok) {
          throw new Error(`: ${alternativePath}`);
        }
        console.log("!");
        return await response.text();
      } catch (backupError) {
        console.error("CSV:", backupError);
        throw error; // 
      }
    } else {
      throw error;
    }
  }
};

const RankingDataLoader = ({ 
  dataSource,
  type = 'csv' // csv
}) => {
  const [rankingData, setRankingData] = useState(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);
  
  useEffect(() => {
    const loadData = async () => {
      try {
        setLoading(true);
        
        console.log(":", dataSource, ":", type);
        
        // GitHub Pages
        const isGitHubPages = window.location.hostname.includes('github.io');
        console.log("GitHub Pages:", isGitHubPages);
        
        let rawData;
        if (type === 'csv') {
          rawData = await readCSV(dataSource);
          console.log("CSV, :", rawData.length);
          console.log("CSV200:", rawData.substring(0, 200));
          
          if (!rawData || rawData.trim().length === 0) {
            console.error(": CSV!");
            throw new Error("CSV");
          }
          
          // CSV
          const rankData = convertCSVToRankingData(rawData);
          setRankingData(rankData);
        } else if (type === 'json') {
          rawData = await readJSON(dataSource);
          setRankingData(rawData);
        }
        
      } catch (err) {
        console.error(":", err);
        setError(err.message);
        
        // 
        if (window.location.hostname.includes('github.io')) {
          console.log("GitHub Pages:");
          console.log("- URL:", window.location.href);
          console.log("- PUBLIC_URL:", process.env.PUBLIC_URL);
          console.log("- :", dataSource);
        }
      } finally {
        setLoading(false);
      }
    };
    
    loadData();
  }, [dataSource, type]);
  
  if (loading) return <div>...</div>;
  if (error) return <div>: {error}</div>;
  if (!rankingData) return <div></div>;
  
  return <Ranking rankingData={rankingData} />;
};

export default RankingDataLoader;