import React, { useState, useEffect } from 'react';
import { Layout, Button, Badge, Avatar, Space, Dropdown } from 'antd';
import { 
  HomeOutlined, 
  TeamOutlined, 
  FileTextOutlined, 
  BellOutlined, 
  MessageOutlined, 
  UserOutlined,
  SettingOutlined,
  LogoutOutlined
} from '@ant-design/icons';
import { useNavigate } from 'react-router-dom';
import { getCurrentDate } from '../services/mockData';

const { Header } = Layout;

const AppHeader = () => {
  const [dateInfo, setDateInfo] = useState({ formattedDate: '', day: '' });
  const navigate = useNavigate();
  
  useEffect(() => {
    const { formattedDate, day } = getCurrentDate();
    setDateInfo({ formattedDate, day });
    
    // 
    const timer = setInterval(() => {
      const { formattedDate, day } = getCurrentDate();
      setDateInfo({ formattedDate, day });
    }, 60000);
    
    return () => clearInterval(timer);
  }, []);
  
  // 
  const userMenuItems = [
    {
      key: 'profile',
      icon: <UserOutlined />,
      label: '',
    },
    {
      key: 'settings',
      icon: <SettingOutlined />,
      label: '',
    },
    {
      type: 'divider',
    },
    {
      key: 'logout',
      icon: <LogoutOutlined />,
      label: '',
    },
  ];

  const handleNavigation = (path) => {
    navigate(path);
  };

  return (
    <Header 
      style={{ 
        backgroundColor: '#0F2656', 
        padding: '0 16px', 
        height: '48px',
        display: 'flex',
        justifyContent: 'space-between',
        alignItems: 'center',
        position: 'fixed',
        zIndex: 1000,
        width: '100%'
      }}
    >
      {/*  */}
      <div style={{ display: 'flex', alignItems: 'center' }}>
        <div className="logo" style={{ color: 'white', fontSize: '18px', fontWeight: 'bold' }}>
          
        </div>
        <div style={{ marginLeft: '24px', color: 'white' }}>
          {dateInfo.formattedDate} {dateInfo.day}
        </div>
      </div>
      
      {/*  */}
      <div style={{ display: 'flex', alignItems: 'center' }}>
        {/*  - onClickLink */}
        <Space size={16}>
          <Button 
            type="text" 
            icon={<HomeOutlined />} 
            style={{ color: 'white' }}
            onClick={() => handleNavigation('/')}
          >
            
          </Button>
          <Button 
            type="text" 
            icon={<TeamOutlined />} 
            style={{ color: 'white' }}
            onClick={() => handleNavigation('/team')}
          >
            
          </Button>
          <Button 
            type="text" 
            icon={<FileTextOutlined />} 
            style={{ color: 'white' }}
            onClick={() => handleNavigation('/milestones')}
          >
            
          </Button>
        </Space>
        
        {/*  */}
        <Space size={16} style={{ marginLeft: '24px' }}>
          <Badge count={5} offset={[0, 0]}>
            <Button 
              type="text" 
              icon={<BellOutlined />} 
              style={{ color: 'white' }} 
            />
          </Badge>
          <Badge count={3} offset={[0, 0]}>
            <Button 
              type="text" 
              icon={<MessageOutlined />} 
              style={{ color: 'white' }} 
            />
          </Badge>
        </Space>
        
        {/*  */}
        <div style={{ marginLeft: '24px' }}>
          <Dropdown
            menu={{ items: userMenuItems }}
            placement="bottomRight"
            arrow
          >
            <Avatar style={{ backgroundColor: '#87d068', cursor: 'pointer' }} icon={<UserOutlined />} />
          </Dropdown>
        </div>
      </div>
    </Header>
  );
};

export default AppHeader; 